














import { Component, Prop, Vue } from "vue-property-decorator";
import smoothReflow from "vue-smooth-reflow";

@Component({
  mixins: [smoothReflow],
})
export default class Collapse extends Vue {
  @Prop(String) name!: string;
  show: boolean = false;

  toggle(): void {
    this.show = !this.show;
  }

  mounted() {
    // @ts-ignore
    this.$smoothReflow({
      transition: "height .25s",
    });
  }
}
